/*HEADER---------------------------------------------------------------*/
header {
  background: -webkit-linear-gradient(45deg, #fe6b83 0%, #9326f0 100%);
  background: -moz-linear-gradient(45deg, #fe6b83 0%, #9326f0 100%);
  background: -o-linear-gradient(45deg, #fe6b83 0%, #9326f0 100%);
  background: -ms-linear-gradient(45deg, #fe6b83 0%, #9326f0 100%);
  background: linear-gradient(45deg, #fe6b83 0%, #9326f0 100%);
  height: 445px;
  text-align: center;
  color: #ffffff;
  position: relative; }

header > h1 {
  font-family: Montserrat, sans-serif;
  color: #ffffff;
  font-size: 4em;
  font-weight: 900;
  transform: scaleY(1.0209); }

#wave-top {
  position: absolute;
  width: 500px;
  right: 0px;
  bottom: -35px; }

#wave-top2 {
  position: absolute;
  width: 370px;
  right: 0px;
  bottom: 64px; }

/*NAVIGATION------------------------------------------------------------------*/
nav {
  list-style-type: none; }

nav a {
  font-family: Montserrat,sans-serif;
  color: #582782;
  font-size: 2em;
  font-weight: 700;
  text-decoration: none;
  padding: 10px;
  border: 1px solid white; }

.navitem {
  display: inline-block;
  margin: 50px 10px; }

